import React, { useEffect, useState } from "react";
//import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
///import "./index.css";
//import server from './../../server.js'
import './../../App.css';
import Select from "react-select"
import 'bootstrap/dist/css/bootstrap.min.css';

const MillOutstanding = () => {
    var [search, setSearch] = useState([])
    let filters = []
    const [Company, setCompany] = useState([]);
    const [Account, setAcc] = useState([]);
    const [data, setData] = useState([]);
    const [pendings, setChkPending] = useState(true);

    const [CompName, setCompanyName] = useState();
    const [AccName, setSelects] = useState();
    let [startDate, setStartDate] = useState(localStorage.getItem('database').replace('sas', '').substring(0, 4) + '-04-01');
    const [endDate, setEndDate] = useState(localStorage.getItem('database').replace('sas', '').substring(4) + '-03-31');

    const options = {
        method: 'POST',
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
        },
        body: JSON.stringify({
            "ipaddress": localStorage.getItem('ipaddress'),
            "port": localStorage.getItem('port'),
            "database": localStorage.getItem('database')
        }),
    };

    useEffect(() => {
        fetch('https://thomasaccapp.onrender.com/AccountMaster', options)
            .then(res => res.json())
            .then(Account => setAcc(Account))
            .catch(err => console.log(err))
    }, [])

    // useEffect(() => {
    //     fetch('http://103.206.139.246:8081/companymaster', options)
    //         .then(res => res.json())
    //         .then(Company => setCompany(Company))
    //         .catch(err => console.log(err))
    // }, [])

    // useEffect(async () => {
    //     const response = await fetch('http://103.206.139.246:8081/companymaster', options)
    //       .then(res => res.json())
    //       //.then(Company => setCompany(Company))
    //       .catch(err => console.log(err))
    //       if (response.length > 0) {
    //         setCompany(response)
    //         setCompanyName({ value: response[0].TrnasID, label: response[0].CoName })
    //       }

    //     }, [])
    useEffect(() => {
        const response = JSON.parse(localStorage.getItem("companymst"))
        setCompany(response)
        if (localStorage.getItem("companylst") != null) {
            setCompanyName({ value: JSON.parse(localStorage.getItem("companylst")).value, label: JSON.parse(localStorage.getItem("companylst")).label })
        } else {
            setCompanyName({ value: response[0].TrnasID, label: response[0].CoName })
        }
    }, [])
    // useEffect(() => {
    //     fetch('http://103.206.139.246:8081/MillOutstanding', options)
    //         .then(res => res.json())
    //         .then(data => setData(data))
    //         .catch(err => console.log(err))
    // }, [])
    const handleChange = (selectedOption) => {
        setSelects(selectedOption);
        //console.log(`Option selected:`, selectedOption);
    }

    const handleCompChange = (selectedCompOption) => {
        setCompanyName(selectedCompOption);
        localStorage.setItem("companylst", JSON.stringify(selectedCompOption));
       //console.log(`Option selected:`, selectedCompOption);
    }

    const shoot = async () => {
        if (CompName == undefined) {
            alert("Company Name is not found..")
            return null;
        }
        if (AccName == undefined) {
            alert("Account Name is not found..")
            return null;
        }
        if (startDate == undefined) {
            alert("From Date Name is not found..")
            return null;
        }

        if (endDate == undefined) {
            alert("To Date Name is not found..")
            return null;
        }

        if ((CompName != undefined) && (AccName != undefined) && (startDate != undefined) && (endDate != undefined)) {

            const options = {
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    "ipaddress": localStorage.getItem('ipaddress'),
                    "port": localStorage.getItem('port'),
                    "database": localStorage.getItem('database'),
                    "comid": CompName.value,
                    "accid": AccName.value,
                    "fromdate": startDate,
                    "todate": endDate,
                    "pending": pendings
                }),
            };

            var sasdata = await fetch('https://thomasaccapp.onrender.com/MillOutstanding', options)
                .then(res => res.json())
                .catch(err => console.log(err))
            sasdata = sasdata.sort((a, b) => new Date(a.acdate) - new Date(b.acdate));

            // filters = data.filter((a) => a.CO_Code = CompName.value &&
            //     a.AG_CODE == AccName.value &&
            //     Number(a.BILLDATE.substr(0, 10).replace(/-/g, "")) >= Number(startDate.replace(/-/g, "")) &&
            //     Number(a.BILLDATE.substr(0, 10).replace(/-/g, "")) <= Number(endDate.replace(/-/g, ""))).sort((a, b) => new Date(a.BILLDATE) - new Date(b.BILLDATE));
            let bdlBal = 0
            let bdlTotQty = 0
            let bdlTotInwQTy = 0
            let bdlTotShort = 0
            let bdlTotBal = 0
            for (let i = 0; i < sasdata.length; i++) {
                bdlTotBal += sasdata[i].QTY - sasdata[i].INW_QTY - sasdata[i].INW_SHRT
                bdlTotQty += sasdata[i].QTY
                bdlTotInwQTy += sasdata[i].INW_QTY
                bdlTotShort += sasdata[i].INW_SHRT
            }
            sasdata.push({ BILL: "Total", Balance: bdlTotBal, QTY: bdlTotQty, INW_QTY: bdlTotInwQTy, INW_SHRT: bdlTotShort })
            search = sasdata
            setSearch(search)

        }
    }
    const Prints = () => {
        window.print()
    }
    const sendWhatsAppMessage = () => {
        if (search.length == 0) {
          alert('The entry cannot be zero');
        } else {
          const AccFilter = Account.filter(acc => {
            return acc.TrnasID === AccName.value;
          });
          AccFilter[0].wappmob = (AccFilter[0].wappmob == 0 ? '' : AccFilter[0].wappmob)
          const whatsappUrl = `https://wa.me/` + AccFilter[0].wappmob + `?text=*Hi%20` + AccFilter[0].AcName.replace('&',' ') + `*,%20your%20Mill%20Oustanding%20balance:%0A*` + search[search.length - 1].Balance.toLocaleString(undefined, { maximumFractionDigits: 2 }) + `*%20Can%20you%20provide%20an%20update%20on%20my%20current%20balance?%20if%20you%20have%20concerns%20about%20your%20account%20balance%20or%20any%20pending%20transactions,%20please%20revet%20us%20Thanks!`;
          window.open(whatsappUrl, '_blank');
        }
      };
    
    return (
        <div className="App" style={{ marginLeft: '10px', marginRight: '10px' }}>
            <h2 style={{ color: 'Pink' }}>Mill Outstanding</h2>
            <label style={{ fontWeight: 'bold' }} htmlFor="">Company Name  : </label>
            <Select options={Company.length == undefined && Company.length == 0 ? null : Company.map((i) => ({ value: i.TrnasID, label: i.CoName }))}
                onChange={handleCompChange}
                value={CompName}
            ></Select>

            <label style={{ fontWeight: 'bold' }} htmlFor="">Account Name  : </label>
            <Select options={Account.length == undefined && Account.length == 0 ? null : Account.map((i) => ({ value: i.TrnasID, label: i.AcName }))}
                onChange={handleChange}
                value={AccName}
            ></Select>

            <label style={{ fontWeight: 'bold' }} htmlFor="datepicker">From Date  : </label>
            <input
                type="date"
                id="datepicker"
                value={startDate}
                onChange={e => setStartDate(e.target.value)}
            />
            {/* //'2017-05-09'// */}
            <span>  </span>
            <input value={pendings} defaultChecked={true} type="checkbox" onChange={e => setChkPending(e.target.checked)} />
            <span> Pending</span>
            <br></br>
            <label style={{ fontWeight: 'bold' }} htmlFor="datepicker">......To date      : </label>
            <input
                type="date"
                id="datepicker"
                value={endDate}
                onChange={e => setEndDate(e.target.value)}
            />
            <span>  </span>
            <button style={{ border: '2px solid ', height: '35px', width: '60px' }} title="Find Record" onClick={shoot} >
                Search
            </button>
            <span>  </span>
            <button style={{ border: '2px solid ', height: '35px', width: '60px' }} title="Print Record" onClick={Prints} >
                Print
            </button>
            <span>  </span>
            <button onClick={sendWhatsAppMessage}>
                <img src={"data:image/png;base64,"} />
                WhatsApp</button>
            <span>  </span>
            <table  >
                <thead>
                    <th style={{ border: '2px solid green' }}>Date</th>
                    <th style={{ border: '2px solid green' }}>Bill No / Lot No</th>
                    <th style={{ textAlign: 'right', border: '2px solid green' }}>QTY</th>
                    <th style={{ textAlign: 'right', border: '2px solid green' }}>Rcd. QTY</th>
                    <th style={{ textAlign: 'right', border: '2px solid green' }}>Short</th>
                    <th style={{ textAlign: 'right', border: '2px solid green' }}>Balance</th>
                    {/* <th>CrAmount</th> */}
                </thead>
                <tbody >
                    {search.map((d) => (
                        <tr key={d}>
                            <td style={{ width: "20%", border: '2px solid green' }}>{d.BILL == 'Total' ? '' : (new Date(d.BILLSDATE).getDate().toString().length == 1 ? "0" + new Date(d.BILLSDATE).getDate().toString() : new Date(d.BILLSDATE).getDate().toString())}{d.BILL == 'Total' ? '' : '/'}{d.BILL == 'Total' ? '' : ((new Date(d.BILLSDATE).getMonth() + 1).toString().length == 1 ? "0" + (new Date(d.BILLSDATE).getMonth() + 1).toString() : (new Date(d.BILLSDATE).getMonth() + 1).toString())}{d.BILL == 'Total' ? '' : '/'}{d.BILL == 'Total' ? '' : new Date(d.BILLSDATE).getFullYear().toString().substring(2)}</td>
                            <td style={d.BILL == 'Total' ? { fontWeight: 'bold', textAlign: 'center', width: "15%", border: '2px solid green' } : { textAlign: 'center', width: "15%", border: '2px solid green' }}>{d.Challan} / {d.LOTNO}</td>
                            <td style={d.BILL == 'Total' ? { fontWeight: 'bold', textAlign: 'right', width: "25%", border: '2px solid green' } : { textAlign: 'right', width: "25%", border: '2px solid green' }}>{Number(d.QTY).toFixed(2)}</td>
                            <td style={d.BILL == 'Total' ? { fontWeight: 'bold', textAlign: 'right', width: "25%", border: '2px solid green' } : { textAlign: 'right', width: "25%", border: '2px solid green' }}>{Number(d.INW_QTY).toFixed(2)}</td>
                            <td style={d.BILL == 'Total' ? { fontWeight: 'bold', textAlign: 'right', width: "25%", border: '2px solid green' } : { textAlign: 'right', width: "25%", border: '2px solid green' }}> {Number(d.INW_SHRT).toFixed(2)}</td>
                            <td style={d.BILL == 'Total' ? { fontWeight: 'bold', textAlign: 'right', width: "25%", border: '2px solid green' } : { textAlign: 'right', width: "25%", border: '2px solid green' }}> {Number(d.Balance).toFixed(2)}</td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    );
};
export default MillOutstanding;